import React, { useState } from "react";
import { DragDropContext } from "react-beautiful-dnd";
import uuid from "react-uuid";
import {onDragEnd} from "./onDragEnd";
import Column from "./Column/Column";
import styled from "styled-components";

const columnsFromBackend = [
    {
        id: uuid(),
        name: "Requested",
        tasks: []
    }
]


const Board = () => {
    const [columns, setColumns] = useState(columnsFromBackend);

    const addTask = (colId) => {
        const currentCol = columns.filter(col => col.id === colId)
        currentCol[0].tasks.push({ id: uuid(), content: "2 task" })
        setColumns(
            columns.map(el => {
                if (el.id === colId) {
                    return  currentCol[0]
                }
                return el
            })
        )
    }

    const delCol = (colId) => {
        setColumns(
            columns.filter(col => col.id !== colId)
        )
    }
    const addCol = () => {
        setColumns([
            ...columns,
            {
                id: uuid(),
                name: "Test",
                tasks: []
            }
        ])
    }
    return (
        <BoardContainer>
            <DragDropContext
                onDragEnd={result => onDragEnd(result, columns)}
            >
                {columns.map((col, index) => {
                    return <Column
                        delCol={delCol}
                        addTask={addTask}
                        col={col}
                        key={index}
                    />
                })}
                <AddColBtn onClick={addCol}>+</AddColBtn>
            </DragDropContext>
        </BoardContainer>
    );
}
export default Board;

const AddColBtn = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 100px;
    color: black;
    background: lightgrey;
    cursor: pointer;
`

const BoardContainer = styled.div`
   marginLeft: 20px; 
   display: flex; 
   justifyContent: flex-start;
   height: 80%; 
   overflow: scroll;
   
   &::-webkit-scrollbar {
        width: 0px;
        background: transparent;
   }
`
