import React, {useEffect, useState} from "react"
import styled from "styled-components";
import {NavLink} from "react-router-dom";
import axios from "axios";
import {useSelector} from "react-redux";
import {H2} from "../StyledComponents/StyledComponents";
import Modal from "../Modal/Modal";

const BoardList = () => {
    const userId = useSelector(state => state.user.userId)
    const [boardsNameList, setBoardsNameList] = useState(['asd', 'asd'])
    const [isModalOpen, setModalOpen] = useState(false)
    console.log(userId)

    useEffect(() => {
        axios.get(`https://sabfesapp.herokuapp.com/api/board/${userId}`)
            .then(res => {
                if (res.status === 200) {}
        })
    }, [userId])

    const addBoard = (nameOfBoard) => {
        setBoardsNameList([...boardsNameList, nameOfBoard])
        setModalOpen(false)
    }

    return <Container>
        <Modal addBoard={addBoard} isModalOpen={isModalOpen} setModalOpen={setModalOpen}/>
        <NavBar>
            <H2 pointer>Все доски</H2>
            <Link to={"/"}>Главная страница</Link>
            <H2 pointer onClick={() => setModalOpen(true)}>Создать доску</H2>
        </NavBar>
        <BoardsContainer>
            {
                boardsNameList.map((board, i) => {
                    return (
                        <Link to={"/boards/123asdc"} key={i}><Board>{board}</Board></Link>
                    )
                })
            }
        </BoardsContainer>
    </Container>
}
export default BoardList


const Board = styled.div`
    padding: 10px;
    width: 200px;
    color: grey;
    height: 120px;
    background: white;
    border: 1px solid lightgrey;
    border-radius: 20px;
    transition: 1s;
    
    &:hover {
        transform: scale(1.03);
    }
`

const BoardsContainer = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 10px;
`

const Link = styled(NavLink)`
    text-decoration: none;
    color: black;
    font-size: 20px;
    font-weight: 300;
`

const Container = styled.div`
    width: 90%;
    margin: 50px auto;
    display: flex;
`

const NavBar = styled.nav`
    padding-left: 50px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
    width: 20%;
`
